*{margin:0;padding:0}html{scroll-behavior:smooth;font-family:"Poppins",sans-serif;color:#2a2a2a;font-size:calc(12px + (20 - 12) * ((100vw - 300px) / (1600 - 300)));overflow-x:hidden}body{background:linear-gradient(-90deg, rgba(255, 255, 255, 0.05) 1px, transparent 1px),linear-gradient(rgba(255, 255, 255, 0.05) 1px, transparent 1px),linear-gradient(-90deg, rgba(255, 255, 255, 0.04) 1px, transparent 1px),linear-gradient(rgba(255, 255, 255, 0.04) 1px, transparent 1px),linear-gradient(transparent 3px, white 3px, white 78px, transparent 78px),linear-gradient(-90deg, white 1px, transparent 1px),linear-gradient(-90deg, transparent 3px, white 3px, white 78px, transparent 78px),linear-gradient(white 1px, transparent 1px),#fff;background-size:4px 4px,4px 4px,80px 80px,80px 80px,80px 80px,80px 80px,80px 80px,80px 80px}::selection{background:#cce200}::-moz-selection{background:#cce200}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:rgba(0,0,0,.5)}::-webkit-scrollbar-thumb{border-radius:0px;background:#2a2a2a}
.App{padding:20px 20px}.App .title_text{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.App a{color:#2a2a2a;transition:color .2s;text-decoration:none}.App a:hover{color:#cce200}@media only screen and (min-width: 1000px){.App{padding:20px 150px}}
.cursor{width:40px;height:40px;border:2px solid #cce200;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;transition:all 150ms ease;transition-property:background-color,opacity,transform,mix-blend-mode;z-index:9999;mix-blend-mode:difference}.cursor--hidden{opacity:0}.cursor--link-hovered{transform:translate(-50%, -50%) scale(1.25);background-color:#cce200}.cursor--clicked{transform:translate(-50%, -50%) scale(0.9);background-color:#cce200}html *,body *{cursor:none}
nav{display:flex;justify-content:center;grid-gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}
footer{position:relative;min-height:100vh}footer .footer__bg{background:#2a2a2a no-repeat center center fixed;background-size:cover;position:absolute;top:0;bottom:-20px;left:-20px;right:-20px;z-index:-1}footer .contact__description{display:flex;flex-direction:column;align-items:flex-end}footer .contact__description p{color:#fff;text-align:right;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));font-weight:600;max-width:calc(200px + (500 - 200) * ((100vw - 300px) / (1600 - 300)));padding:50px 0}footer .contact__description a{color:#fff}footer .copyright{color:#fff;display:flex;position:absolute;bottom:0px;left:0;right:0;justify-content:center}@media only screen and (min-width: 1000px){footer .footer__bg{left:-200px;right:-200px}}
.about{display:flex;flex-direction:column;height:calc(100vh - 20px)}.about p{margin-top:10px}.about .about__intro{text-align:right;flex-direction:column;margin-top:100px}.about .about__intro .about__intro__title h1:last-child{margin-top:-10px}.about .about__intro .about__intro__subtitle{font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)))}.about .about__description{margin-top:50px;transition:.5s max-height cubic-bezier(0.19, 1, 0.22, 1);overflow:hidden}.about .about__description__show{max-height:300px}.about .about__description__show a{text-decoration:underline}.about .about__description__hide{max-height:0px}.about .about__more{text-align:right;position:relative;margin-top:15px}
#work{margin-bottom:100px}#work h1{margin-bottom:-10px}#work .work__category{margin:50px 0;display:flex;justify-content:center;grid-gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}#work .work__category .activeOption{text-decoration:underline}#work .work__grid{-webkit-column-count:2;column-count:2;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}#work .work__grid .work__item{margin:0;display:grid;grid-template-rows:1fr auto;margin-bottom:10px;page-break-inside:avoid;-webkit-column-break-inside:avoid;break-inside:avoid}#work .work__grid .work__item img{width:100%;display:block;grid-row:1/-1;grid-column:1}#work .work__grid .work__item p{grid-row:2;grid-column:1;background-color:rgba(255,255,255,.5);padding:.2em .5em;justify-self:start}@media only screen and (min-width: 700px){#work .work__grid{-webkit-column-count:3;column-count:3}}
.work__page{margin:50px 0;display:flex;position:relative;flex-direction:column;align-items:center;min-height:calc(100vh - 70px)}.work__page .work__page__content{display:grid;grid-template-columns:2fr 3fr;grid-gap:10px;gap:10px}.work__page .work__page__content .temp_images{width:100%;background-color:#000}@media only screen and (max-width: 750px){.work__page .work__page__content{grid-template-columns:1fr}}
.resume{margin-top:150px}.resume h1{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.resume h2{font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));margin:30px 0}.resume .resume__section{padding:20px;border-bottom:solid #2a2a2a 1px}.resume .resume__item{margin:20px}
